.App {
    text-align: center;
    width: 100%;
}

.App-header {
    background-color: #1B5E20;
    height: 30px;
    padding-right: 20px;
    padding-bottom: 20px;
    padding-top: 1px;
    color: white;
    width: 100%;
    z-index: 9999999999999999999999999;
    top: 0;
}

.App-title {
    font-size: 1.5em;
}

.footer {
    width: 100%;
    height: 5%;
    position: fixed;
    bottom: 0;
    background: #1B5E20;
    -webkit-transition-property: all;
    -o-transition-property: all;
    transition-property: all;
    -webkit-transition-duration: 500ms;
         -o-transition-duration: 500ms;
            transition-duration: 500ms;
    z-index: 99999999;
}


